.burger {
  display: none;
}
@media (max-width: 1024px) {
  .burger {
    display: block;
    width: 37px;
    height: 22px;
    position: relative;
    margin-left: 20px;
  }
  .burger::before, .burger::after {
    content: "";
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 3px;
    transition: all 0.3s ease 0s;
  }
  .burger::before {
    top: 0;
  }
  .burger span {
    position: absolute;
    left: 0;
    top: 10px;
    background-color: #fff;
    width: 100%;
    height: 3px;
    transition: all 0.3s ease 0s;
  }
  .burger::after {
    bottom: 0;
  }
  .burger.active::before {
    top: 10px;
    transform: rotate(45deg);
  }
  .burger.active span {
    opacity: 0;
    width: 0;
  }
  .burger.active::after {
    bottom: 10px;
    transform: rotate(-45deg);
  }
}
@media (max-width: 576px) {
  .burger {
    margin-left: 11px;
  }
}

